// @ts-nocheck

import "./App.css";
import React, {
  useEffect,
  useState,
} from "react";

//ui
import { Button, makeStyles, createStyles, Theme } from "@material-ui/core";

import { DAppClient } from "@airgap/beacon-sdk";

import { TezosToolkit, OpKind } from '@taquito/taquito'
import {
  BeaconWallet,
} from '@taquito/beacon-wallet'

const Tezos = new TezosToolkit('https://mainnet.api.tez.ie')
const wallet = new BeaconWallet({
  name: 'hicetnunc.xyz',
  preferblackNetwork: 'mainnet',
})
Tezos.setWalletProvider(wallet)


const dAppClient = new DAppClient({ name: "Beacon Docs" });
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
    },
    sizeBig: {
      width: theme.spacing(12),
      height: theme.spacing(12),
    },
    sizeSmall: {
      width: theme.spacing(6),
      height: theme.spacing(6),
    },
  }),
);

function App() {

  const classes = useStyles();
  const [activeAccount, setActiveAccount] = useState();
  const [synced, setSynced] = useState('sync');
  const [showUnsync, setShowUnsync] = useState(false);
  const isMobile = window.innerWidth <= 500;
  const [objkts, setObjkts] = useState([]);


  useEffect(() => {
    async function getDRUMBalance(address) {
      let result = 0;
      fetch('https://api.tzstats.com/explorer/bigmap/52426/values')
        .then(response => response.json())
        .then(data => {
          for (let i = 0; i < data.length; i++) {
            if (data[i].key[0] === address) {
              result = data[i].value;
              console.log(result)
            }
          }
        })
    }

    if (activeAccount)
      getDRUMBalance(activeAccount.address);

  }, [activeAccount]);

  useEffect(() => {
    async function fetchGraphQL(operationsDoc, operationName, variables) {
      let result = await fetch('https://hdapi.teztools.io/v1/graphql', {
        method: 'POST',
        body: JSON.stringify({
          query: operationsDoc,
          variables: variables,
          operationName: operationName,
        }),
      })

      var ress = await result.json();
      return ress;
    }

    async function fetchCollection(addr) {
      const { errors, data } = await fetchGraphQL(
        query_collection,
        'collectorGallery',
        { address: addr }
      )
      if (errors) {
        console.error(errors)
      }
      const result = data ? data.hic_et_nunc_token_holder : null;
      console.log("result")
      console.log(result)
      setObjkts(result)
      return result
    }


    fetchCollection("KT1XgZSkrPfEyfjeBtionEquy54hyMMa6HL5");
  }, [activeAccount]);

  const HashToURL = (hash, type) => {
    // when on preview the hash might be undefined.
    // its safe to return empty string as whatever called HashToURL is not going to be used
    // artifactUri or displayUri
    if (hash === undefined) {
      return ''
    }

    switch (type) {
      case 'HIC':
        return hash.replace('ipfs://', 'https://pinata.hicetnunc.xyz/ipfs/')
      case 'CLOUDFLARE':
        return hash.replace('ipfs://', 'https://cloudflare-ipfs.com/ipfs/')
      case 'PINATA':
        return hash.replace('ipfs://', 'https://gateway.pinata.cloud/ipfs/')
      case 'IPFS':
        return hash.replace('ipfs://', 'https://ipfs.io/ipfs/')
      case 'INFURA':
        try {
          var cidv1 = new ipfsClient.CID(hash.replace('ipfs://', '')).toV1()
          var subdomain = cidv1.toBaseEncodedString('base32')
          return `https://${subdomain}.ipfs.infura-ipfs.io/`
        } catch (err) {
          return undefined
        }
      case 'DWEB':
        return hash.replace('ipfs://', 'http://dweb.link/ipfs/')
      default:
        console.error('please specify type')
        return hash
    }
  }

  //fetch wallet name if it exist for example, trydrum.tez
  async function getDomain(address: string) {
    let domain;
    await fetch('https://api.tezos.domains/graphql', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        query: `
            {
              reverseRecord(address: "`+ address + `"){owner domain{name}}
            }
            `,
        variables: {
        },
      }
      ),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.data.reverseRecord) {
          domain = result.data.reverseRecord.domain.name;
          setSynced(domain);
        }
      });
  }

  useEffect(() => {
    async function getAcc() {
      setActiveAccount(await dAppClient.getActiveAccount());
      if (activeAccount) {
        setSynced(activeAccount.address.slice(0, 6) + "..." + activeAccount.address.slice(32, 36));
        setShowUnsync(true);
        getDomain(activeAccount.address);
      }
      else {
        setSynced('sync');
        setShowUnsync(false);
      }
    }
    getAcc();
  }, [activeAccount]);



  async function unsync() {
    setActiveAccount(await dAppClient.getActiveAccount())
    if (activeAccount) {
      // User already has account connected, everything is ready
      dAppClient.clearActiveAccount().then(async () => {
        setActiveAccount(await dAppClient.getActiveAccount())
        setSynced('sync');
        setShowUnsync(false);
      });
    }
  }

  async function sync() {
    setActiveAccount(await dAppClient.getActiveAccount())
    //Already connected
    if (activeAccount) {
      setSynced(activeAccount.address)
      setShowUnsync(true);
      getDomain(activeAccount.address);

      return activeAccount;
    }
    // The user is not synced yet
    else {
      try {
        console.log("Requesting permissions...");
        const permissions = await dAppClient.requestPermissions();
        setActiveAccount(await dAppClient.getActiveAccount())
        console.log("Got permissions:", permissions.address);
        setSynced(permissions.address)
        setShowUnsync(true);
        getDomain(permissions.address);

      }
      catch (error) {
        console.log("Got error:", error);
      }
    }
  }


  return (
    <>

      <div style={{ height: "100vh" }} >
        <div style={{ fontSize: isMobile ? "1em" : "1.5em", textAlign: "center", padding: 10 }} >
          <b>DRUM MARKETPLACE</b>
        </div>
        <div style={{ display: isMobile ? "grid" : "flex", width: "100vw", flexWrap: "wrap" }}>
          {isMobile &&
            objkts.map((objkt, index) => (

              <div style={{ padding: "5vw", width: "90vw" }}>
                <div style={{ textAlign: "center" }}> {objkt.token.title} </div>
                < img src={HashToURL(objkt.token.display_uri, 'IPFS')} alt={objkt.token.title} width="100%" ></img>
                <div style={{ textAlign: "center" }}> 1000 DRUM </div>
                <Button size={isMobile ? "small" : "medium"} title={"buy"}
                  onClick={async () => {
                    if (activeAccount) {
                      console.log("objkt.token.id");
                      let drum = await Tezos.wallet.at("KT1DX7UdxtzW7bEBihW31kV8Ge4MojAgrLNW")
                      let marketplace = await Tezos.wallet.at("KT1XgZSkrPfEyfjeBtionEquy54hyMMa6HL5")
                      console.log(objkt.token.id);
                      console.log(activeAccount.address);
                      let list = [
                        {
                          kind: OpKind.TRANSACTION,
                          ...drum.methods.update_operators([{ add_operator: { operator: "KT1XgZSkrPfEyfjeBtionEquy54hyMMa6HL5", token_id: 0, owner: activeAccount.address } }])
                            .toTransferParams({ amount: 0, mutez: true, storageLimit: 175 })
                        },
                        {
                          kind: OpKind.TRANSACTION,
                          ...marketplace.methods.default({ value: objkt.token.id }).toTransferParams({ amount: 0, mutez: true, storageLimit: 300 })
                        }
                      ]

                      let batch = await Tezos.wallet.batch(list);
                      return await batch.send()
                    }
                    else {
                      sync()
                    }
                  }} style={{ width: "100%" }} ><u> Buy</u> </Button>
              </div>
            ))
          }

          {!isMobile &&
            objkts.map((objkt, index) => (
              <div style={{ padding: "1vw", width: "18vw" }}>
                <div style={{ textAlign: "center" }}> {objkt.token.title} </div>
                < img src={HashToURL(objkt.token.display_uri, 'IPFS')} alt={objkt.token.title} width="100%" ></img>
                <div style={{ textAlign: "center" }}> 1000 DRUM </div>
                <Button size={isMobile ? "small" : "medium"} title={"buy"}
                  onClick={async () => {
                    if (activeAccount) {

                      console.log("objkt.token.id");
                      let drum = await Tezos.wallet.at("KT1DX7UdxtzW7bEBihW31kV8Ge4MojAgrLNW")
                      let marketplace = await Tezos.wallet.at("KT1XgZSkrPfEyfjeBtionEquy54hyMMa6HL5")
                      console.log(objkt.token.id);
                      console.log(activeAccount.address);
                      let list = [
                        {
                          kind: OpKind.TRANSACTION,
                          ...drum.methods.update_operators([{ add_operator: { operator: "KT1XgZSkrPfEyfjeBtionEquy54hyMMa6HL5", token_id: 0, owner: activeAccount.address } }])
                            .toTransferParams({ amount: 0, mutez: true, storageLimit: 175 })
                        },
                        {
                          kind: OpKind.TRANSACTION,
                          ...marketplace.methods.default(objkt.token.id).toTransferParams({ amount: 0, mutez: true, storageLimit: 300 })
                        }
                      ]

                      let batch = await Tezos.wallet.batch(list);
                      return await batch.send()
                    }
                    else {
                      sync()
                    }
                  }} style={{ width: "100%" }} ><u> Buy</u> </Button>

              </div>

            ))
          }
        </div>

        <div className="bottom" style={{ position: isMobile ? "sticky" : "absolute", display: "flex", alignItems: "center", justifyContent: "flex-end", backgroundColor: "white" }} >

          {showUnsync && <Button size={isMobile ? "small" : "medium"} title={"unsync"} onClick={() => { unsync() }} ><u>unsync</u> </Button>}

          {showUnsync && <div> | </div>}
          <Button title={"sync"} size={isMobile ? "small" : "medium"} onClick={async () => { await sync(); }} ><u>{synced}</u> </Button>
        </div>


      </div >
    </>
  );
}

export default App;

const query_collection = `
query collectorGallery($address: String!) {
  hic_et_nunc_token_holder(where: {holder_id: {_eq: $address}, token: {creator: {address: {_neq: $address}}}, quantity: {_gt: "0"}}, order_by: {token_id: desc}) {
	token {
	  id
	  artifact_uri
	  display_uri
	  thumbnail_uri
	  timestamp
	  mime
	  title
	  description
	  supply
	  royalties
	  creator {
		address
	  }
	}
  }
}
`
